@charset "UTF-8";
/* reset.css */
html,body{width:100%;height:100%;}
body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,dl,dt,dd,ul,li,ol,form,fieldset,input,textarea,th,td,table{margin:0;padding:0;}
small,b,i,article,aside,canvas,details,figcaption,figure,footer,menu,nav,section,time,mark,audio,video,button,input {margin:0;padding:0;}
article,aside,details,figcaption,figure,footer,header,menu,nav,section,video {display:block;}
table{border-collapse:collapse;}
img,fieldset{border:none;}
address,caption,cite,code,em,strong,th{font-style:normal;font-weight:normal;}
ol,ul{list-style:none;}
caption, th {text-align:left;}
h1,h2,h3,h4,h5,h6{font-weight:normal;font-size:100%;}
q:after, q:before{content:"";}
abbr,acronym{border:none;}
hr{display:none;}
.blind,#skipNavi {position:absolute;left:0;top:0;width:0;height:0;font-size:0;line-height:0;text-indent:-9999px;overflow:hidden;visibility:hidden;}
img,input{vertical-align:middle;}
img{max-width:100%;}
/* width값이 %로 된 이미지들이 브라우저 쪽이 줄어들 경우, 비례를 맞춰 축소하도록 하기 위해 사용 */