// Chats.scss
main#Chats {
    padding:64px 0 54px;
    // height:calc(100vh - 6.4vh - 64px);
    /* tab_bar의 높이:6.4%, header:64px */
    
    /* search_box */
    .search_box {
        display:flex;flex-direction:row;justify-content:center;align-items:center;
        height:7.3%;min-height:53px;
    
        .search_inner {
            display:flex;flex-direction:row;justify-content:space-between;align-items:center;
            width:88%;height:50%;
            background-color:#f1f2f4;border-radius:5px;
            i {font-size:16px;color:#bebcc1;align-self:center;margin-left:15px;}
            input {
                width:90%;background:transparent;border:none;padding:0 15px;
                &::placeholder {text-align:center;}
            }
        }
    }

    /* main_section */
    .main_section{
        ul {
            display:flex;flex-direction:column;border-top:1px solid #ebebeb;
            
            li {
                border-bottom:1px solid #ebebeb;
        
                a {
                    display:flex;flex-wrap:wrap;align-items:center;font-size:16px;
                
                    .chats_img {width:50px;height:50px;margin:15px;}
                    .chats_cont {
                        flex:auto;display:flex;flex-direction:column;justify-content:center;
                
                        .chats_name {font-weight:700;margin-bottom:5px;}
                        .chats_lastest {font-size:12px;color:#8e9399;}
                    }
                    .chats_time {margin-right:15px;font-size:12px;color:#8e9399;}
                }
            }
    
        }
    }
    
    /* floating Btn */
    .chat_fa_btn {
        position:fixed;right:20px;bottom:80px;
    
        a {
            display:flex;justify-content:center;align-items:center;
            width:60px;height:60px;
            background:#ffe84c;border-radius:50%;box-shadow:0 2px 6px #0004;
            font-size:18px;color:#513736;
            transition:color 300ms linear 0s,background 300ms linear 0s;
        
            &:hover {background:#513736;color:#ffe84c;}
        }
    }
}

