/* common.css */
@import url(reset.css);

html,body {width:100%;}
body {font-size:12px;}
a:link, a:visited {color:#000;text-decoration:none;}
a:hover, a:focus, a:active {color:#000;text-decoration:none;}
img {max-width:100%;}

.empty {display:block;width:40px;height:40px;margin:10px 15px;border-radius:50%;background:url(images/empty.jpg) 0 0 no-repeat;background-size:cover;}